      const isSmall = $(window).width() < 1320;
      if (isSmall && !$(selector).hasClass("slick-initialized")) {
        initSlider(selector);
      }
      if (!isSmall && $(selector).hasClass("slick-initialized")) {
        destroySlider(selector);
      }
